import navigationBar from '../../components/navigationBar.vue'
import {get} from '../../api/homepage.js'
import homepage from '../detail/homepage.vue'
import lock from '../detail/lock.vue'
import interactive from '../detail/interactive.vue'
export default {
	components:{
		navigationBar,
		homepage,
		lock,
		interactive
	},
	data() {
		return {
		  navBarHeight: 0,
		  fixtop: false,
		  videoTitle: '瑞宝星微程序开发',
		  imgs:'',
		  tabIndex: 0,
		  scrollInto: "",
		  tabBars:[{
			  name:'主页',
			  id:'zhuye',
			  img:"/static/zhuye.png",
			},{
			  name:'方案',
			  id:'fangan',
			  img:"/static/fangan.png",
			},{
			  name:'动态',
			  id:'dongtai',
			  img:"/static/news.png",
		  }]
		  
		}
	},
	  beforeMount() {
	    const self = this
	    wx.getSystemInfo({
	      success(system) {
	        console.log(`system:`, system)
	        self.statusBarHeight = system.statusBarHeight
	        let platformReg = /ios/i
	        if (platformReg.test(system.platform)) {
	          self.titleBarHeight = 44
	        } else {
	          self.titleBarHeight = 48
	        }
	        self.navBarHeight = self.statusBarHeight + self.titleBarHeight
			console.log('?'+self.navBarHeight)
	      }
	    })
	  },
	
	onLoad() {
		this.getimgs()
		let that = this
		    const query = wx.createSelectorQuery()
		    query.select(".scroll-h",".uni-tab-item").boundingClientRect()
		    query.exec(function(res) {
		      that.top = res[0].top -that.navBarHeight
		    })
		  },
		   onPageScroll: function(e) {
		      var that = this
		      console.log(that.top - that.navBarHeight)
		      if (e.scrollTop >= that.top) {
		        that.fixtop = true
		      } else {
		        that.fixtop = false
		    }
	},
	methods: {
		async getimgs() {
			const res = await get('/api/chen/banners',{
				owner: 'chenxing'
			})
			this.imgs = res.banner
		},
		ontabtap(e) {
		    let index = e.target.dataset.current || e.currentTarget.dataset.current;
			this.tabIndex = index
		},
	}
}
